import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, MetaDataType, Tab } from 'shared-code-lib';
import { Component as DWFComponent } from '../../../../blocks/models/component';
import { InstalledComponent } from '../../../../blocks/models/deployments';
@Component({
  selector: 'app-installed-component-modal',
  templateUrl: './installed-component-modal.component.html',
  styleUrls: ['./installed-component-modal.component.scss'],
})
export class InstalledComponentModalComponent implements OnInit {
  @Input() installedComponent: InstalledComponent;
  @Input() components: Array<DWFComponent>;
  @Output() variablePending = new EventEmitter<boolean>();

  fieldComponent: FieldDefinition;
  fieldVersion: FieldDefinition;
  fieldInstalled: FieldDefinition;
  tabs = [];

  activeTab = 'general';

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createTabs();

    this.createFields();
  }

  createTabs(): void {
    this.tabs.push(new Tab('general', this.translateService.instant('installed-component-modal.general')));
    this.tabs.push(new Tab('variables', this.translateService.instant('installed-component-modal.variables')));
    this.tabs.push(
      new Tab('configuratorSettings', this.translateService.instant('installed-component-modal.configurator-settings'))
    );
  }

  createFields = (): void => {
    const componentsOptions = this.components.map((component) => {
      return { name: component.name, value: component.id.toString() };
    });

    this.fieldComponent = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('installed-component-modal.component'),
      required: false,
      datasource: new FixedDataSource(componentsOptions),
    });

    this.fieldVersion = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('installed-component-modal.version'),
      placeholder: this.translateService.instant('installed-component-modal.version-placeholder'),
      required: true,
    });
  };

  onComponentChanged($event: string): void {
    this.installedComponent.version = null;
  }

  activateTab = (view: string): void => {
    this.activeTab = view;
  };

  onVariablePending(isPending: boolean): void {
    this.variablePending.emit(isPending);
  }
}
