import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminMachineComponent } from './admin-machine.component';

const routes: Routes = [
  {
    path: 'duplicate/:machineId',
    component: AdminMachineComponent,
    data: { type: 'duplicate' },
    pathMatch: 'full',
  },
  {
    path: 'create',
    component: AdminMachineComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':machineId',
    component: AdminMachineComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminMachineRoutingModule {}
