import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType, Tab } from 'shared-code-lib';
import { Log } from '../../blocks/core/log';
import { IGrouping, IMachine, Machine, Variables } from '../../blocks/models/deployments';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-admin-machine',
  templateUrl: './admin-machine.component.html',
  styleUrls: ['./admin-machine.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminMachineComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;

  saveButton: Control;
  cancelButton: Control;
  machine: Machine;

  fieldName: FieldDefinition;
  fieldGrouping: FieldDefinition;
  tabs = [];

  activeTab = '';

  components = [];

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    public ngxSmartModalService: NgxSmartModalService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        if (this.routeData.type == 'create') {
          this.machine = new Machine({
            name: '',
            variables: new Variables(),
            groupingId: '',
            id: '',
            installedComponents: [],
          } as IMachine);
        } else {
          this.loadItems();
        }
      })
    );

    this.createButtons();
    this.createFields();
    this.createTabs();
    this.loadComponents();
  }

  private loadComponents(): void {
    this.hydraApi.getComponents(
      (apiResult: ApiResult) => {
        if (apiResult.success) {
          this.components = apiResult.object;
          this.activeTab = 'components';
        } else {
          (err: any) => console.error(err);
        }
      },
      (err: any) => console.error(err)
    );
  }

  private createTabs(): void {
    this.tabs.push(new Tab('components', this.translate.instant('admin-machine.components')));
    this.tabs.push(new Tab('variables', this.translate.instant('admin-machine.variables')));
  }

  private createButtons = (): void => {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.saveButton.setTranslation(this.translate.instant('Common.Save'));
    this.cancelButton.setTranslation(this.translate.instant('Common.Cancel'));
  };

  private createFields = (): void => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-machine.name'),
      placeholder: this.translate.instant('admin-machine.name-placeholder'),
      required: true,
    });

    this.fieldGrouping = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-machine.grouping'),
      placeholder: this.translate.instant('admin-machine.grouping-placeholder'),
      required: false,
      datasource: new GroupingDataSource(this.hydraApi, this.translate),
    });
  };

  CreateField = (label: string, required: boolean = false): FieldDefinition => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant(label),
      placeholder: this.translate.instant(label),
      required: required,
    });
  };

  onSave = (): void => {
    if (this.machine.id) {
      this.hydraApi.updateMachine(this.machine, this.machineSaved, this.machineSaveFailed);
    } else {
      this.hydraApi.createMachine(this.machine, this.machineSaved, this.machineSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  machineSaved = (result: ApiResult): void => {
    if (result.success) {
      this.toastr.success(this.translate.instant('admin-machine.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-machine.validation-errors');
    }
    this.spinner.hide();
  };

  machineSaveFailed = (error: any) => {
    this.toastr.error(this.translate.instant('admin-machine.save-failed'));
    Log.error('admin-machine.save-failed', error);
    this.spinner.hide();
  };

  onCancel = (): void => {
    this.returnToGrid();
  };

  returnToGrid = (): void => {
    this.router.navigateByUrl(
      'product/settings/BambaDWFToolsApplication/BambaDWFToolsApplication-Upland.Bamba.DWFTools.IFXReleaseManager.Machines.Folder'
    );
  };

  machineLoaded = (ApiResult: any): void => {
    if (this.routeData.type === 'duplicate') {
      ApiResult.object.name = `${this.translate.instant('Admin.CopyOf')} ${ApiResult.object.name}`;
      ApiResult.object.id = '';
    }
    this.machine = new Machine({ ...ApiResult.object });
    this.cdr.markForCheck();
    this.spinner.hide();
  };

  machineFailed = (error: any): void => {
    this.toastr.error(this.translate.instant('admin-machine.load-failed'));
    Log.error('admin-machine.load-failed', error);
    this.spinner.hide();
  };

  loadItems = (): void => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.spinner.show(this.translate.instant('admin-machine.loading-machine'), document.body);
      this.hydraApi.getMachine(this.routeParams.machineId, this.machineLoaded, this.machineFailed);
    }
  };

  activateTab = (view: string) => {
    this.activeTab = view;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}

class GroupingDataSource extends FixedDataSource {
  constructor(private hydraApiService: HydraApiService, private translateService: TranslateService) {
    super();
  }

  successCallBack: (items: IFieldOptions[]) => void;
  failureCallBack: (err: any) => void;

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    this.successCallBack = successCallBack;
    this.failureCallBack = failureCallBack;

    this.hydraApiService.getGroupings(this.groupingsLoaded, this.failed);
  }

  groupingsLoaded = (result: ApiResult): void => {
    if (!result.success) return;

    const options = new Array<IFieldOptions>();
    result.object.forEach((grouping: IGrouping) => {
      options.push({ name: grouping.name, value: grouping.id });
    });

    this.successCallBack(options);
  };

  failed = (err): void => {
    this.failureCallBack(err);
  };
}
