import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ColumnDisplayType, ICombinedColumn, TableItem } from 'shared-code-lib';
import { Component as DWFComponent } from '../../../blocks/models/component';
import { InstalledComponent } from '../../../blocks/models/deployments';

@Component({
  selector: 'app-installed-component-table',
  templateUrl: './installed-component.component.html',
  styleUrls: ['./installed-component.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class InstalledComponentComponent implements OnInit {
  items: TableItem[] = [];
  columns: ICombinedColumn[];
  sort: SortDescriptor;
  preferencesKey: string;
  numberOfDisplayedRows: number = 5;
  isVariablePending = false;

  @Input() installedComponents: Array<InstalledComponent> = [];
  @Input() components: Array<DWFComponent> = [];
  errors: any[];
  showInstalledComponentModal: boolean;
  installedComponentModalKey: string;
  modalEditInstalledComponent: InstalledComponent;
  installedComponentModalTitle: any;

  constructor(
    private translate: TranslateService,
    private ngxSmartModalService: NgxSmartModalService
  ) {}

  ngOnInit(): void {
    const defaultSort: SortDescriptor = {
      field: '1',
      dir: 'asc',
    };

    this.sort = defaultSort;

    this.columns = this.getInstalledComponentColumns();

    this.translateColumnHeaderText();

    this.createItems();
  }

  translateColumnHeaderText = (): void => {
    for (const c of this.columns) {
      if (!c.headerText) continue;
      c.headerText = this.translate.instant(c.headerText);
    }
  };

  closeConfirmModal = () => this.ngxSmartModalService.getModal('actionConfirmModal').close();

  createInstalledComponentTableItem(installedComponent: InstalledComponent): TableItem {
    if (this.components) {
      const component = this.components.find((c) => c.id === installedComponent.componentId);
      installedComponent['componentName'] = component ? component.name : '';
    }

    const tableItem = new TableItem(installedComponent, this.columns, this.translate);

    tableItem.data.defaultAction = {
      name: this.translate.instant('installed-component.properties'),
    };

    return tableItem;
  }

  getInstalledComponentColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(
      this.createColumn(
        'componentName',
        ColumnDisplayType.ActionLink,
        this.translate.instant('installed-component.component-name'),
        200,
        'componentName',
        true
      )
    );

    columns.push(
      this.createColumn(
        'version',
        ColumnDisplayType.Text,
        this.translate.instant('installed-component.component-version'),
        200,
        'version',
        true
      )
    );

    columns.push(
      this.createColumn(
        'dateInstalledUTC',
        ColumnDisplayType.DateTime,
        this.translate.instant('installed-component.component-installed'),
        200,
        'dateInstalledUTC',
        true
      )
    );

    return columns;
  }

  createColumn(
    id: string,
    displayType: number,
    columnName: string,
    width: number,
    itemProperty: string,
    stretch = false
  ): ICombinedColumn {
    return {
      id: 'column_' + id,
      width: width,
      stretch: stretch,
      displayType: displayType,
      headerImage: '',
      headerText: columnName,
      itemProperty: itemProperty,
      columnName: columnName,
      sortable: false,
      sortByFieldName: '',
      invertBooleanValue: false,
    };
  }

  showAddModal(): void {
    this.errors = [];
    this.installedComponentModalKey = '';
    this.modalEditInstalledComponent = new InstalledComponent({});
    this.installedComponentModalTitle = this.translate.instant(
      'installed-component.add-installed-component-modal-title'
    );

    this.ngxSmartModalService.getModal('installedComponentModal').open();
    this.showInstalledComponentModal = true;
  }

  showUpdateModal = (action: any): void => {
    this.errors = [];
    this.modalEditInstalledComponent = new InstalledComponent(action.item);
    this.installedComponentModalKey = action.item.componentId;
    this.installedComponentModalTitle = this.translate.instant(
      'installed-component.installed-component-properties-modal-title'
    );

    this.ngxSmartModalService.getModal('installedComponentModal').open();
    this.showInstalledComponentModal = true;
  };

  onDeleteInstalledComponentModal = (action: any): void => {
    const index = this.installedComponents.findIndex((r) => r.componentId == action.item.data.componentId);
    this.installedComponents.splice(index, 1);
    this.createItems();
  };

  onSaveInstalledComponentModal = (): void => {
    if (!this.isInstalledComponentModalValid()) return;
    if (this.installedComponentModalKey) {
      let index = this.installedComponents.findIndex((r) => r.componentId === this.installedComponentModalKey);
      if (index >= 0) {
        this.installedComponents[index] = this.modalEditInstalledComponent;
      }
    } else {
      this.installedComponents.push(this.modalEditInstalledComponent);
    }

    this.createItems();

    this.onCloseInstalledComponentModal();
  };

  onCloseInstalledComponentModal = (): void => {
    this.ngxSmartModalService.getModal('installedComponentModal').close();
    this.showInstalledComponentModal = false;
    this.isVariablePending = false;
  };

  private createItems(): void {
    this.items = this.installedComponents.map((e) => this.createInstalledComponentTableItem(e));
  }

  isInstalledComponentModalValid(): boolean {
    this.errors = [];

    if (this.isVariablePending) {
      this.errors.push(this.translate.instant('installed-component.pending-variable'));
      return false;
    }

    if (this.modalEditInstalledComponent.componentId) {
      if (
        this.installedComponents.some(
          (a) =>
            a.componentId == this.modalEditInstalledComponent.componentId &&
            a.componentId != this.installedComponentModalKey
        )
      ) {
        this.errors.push(this.translate.instant('installed-component.component-unique'));
        return false;
      } else {
        return true;
      }
    }

    this.errors.push(this.translate.instant('installed-component.must-select-component'));
    return false;
  }

  onVariablePending(isPending: boolean): void {
    this.isVariablePending = isPending;
  }
}
